@import "../../../scss/variables";

.main {
  .card {
    max-width: 800px;
    max-height: 90vh;
  }

  .card-body {
    overflow: auto;
  }

  .msg-box {
    max-width: 75%;
  }

  .time_date {
    color: #747474;
    display: block;
    font-size: 10px;
    font-style: italic;
  }

  .message-content {
    background: #ebebeb none repeat scroll 0 0;
    border-radius: 15px;
    color: #646464;
    font-size: 14px;
    margin-left: 0;
    padding: 5px 10px 5px 12px;
    width: 100%;
  }

  .notification {
    color: #646464;
    font-size: 12px;
  }

  p {
    margin-bottom: 0px;
  }

  .disclaimer {
    font-size: 14px;
  }

  .file-list {
    overflow: hidden;
    word-break: break-word;
    justify-content: center;
    margin-bottom: 1rem;
  }

  .file-name {
    font-size: 12px;
    font-style: italic;
    margin: 2px 0px;
  }

  .user {
    overflow: hidden;
    margin: 10px 0;

    &__avatar {
      max-width: 120px;
      display: flex;
      flex-direction: column;
      align-items: center;

      &__name {
        font-size: 12px;
        font-weight: 600;
        color: $orange;
        margin-top: 5px;
      }
    }
  }
}

@media (max-width: 575.98px) {
  .main {
    .msg-box {
      max-width: 60%;
    }
  }
}

.client-screen-messages .quill .ql-container .ql-editor,
.client-screen-messages .quill .ql-container .ql-editor:focus {
  color: black;
}
